body {
  margin: 0;
  font-family:'Jameel Noori Nastaleeq Regular',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: rgb(42, 209,173);
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */
h1 {
  font-size: 30px;
  

}
h2 {
  font-size: 25px;
  
}
button {
  font-family: 'Jameel Noori Nastaleeq Regular';
  margin:1rem;
  margin-top: 20px;

  padding: 5px;
  border: none;
  height: 5rem;
  width: 20rem;
  border-radius: 40px;
  font-size: 1.5rem;
  background-color: rgb(7, 89, 104);
  color: #fff;
  cursor: pointer;
}

